html, body {
    width: 100vw;
    height: 100vh;
    padding: 0;
    margin: 0;
    overflow: hidden;
    -webkit-font-smoothing: antialiased;
    -webkit-text-size-adjust: 100%;
    -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
    -webkit-overflow-scrolling: touch;
    box-sizing: border-box;
}

.bg1 {
    background: rgb(63, 94, 251);
    background: linear-gradient(90deg, rgba(63, 94, 251, 1) 0%, rgba(252, 70, 107, 1) 100%);
}

.bg2 {
    background: rgb(131, 58, 180);
    background: linear-gradient(90deg, rgba(131, 58, 180, 1) 0%, rgba(253, 29, 29, 1) 50%, rgba(252, 176, 69, 1) 100%);
}

.bg3 {
    background-color: #21D4FD;
    background-image: linear-gradient(19deg, #21D4FD 0%, #B721FF 100%);
}

.bg4 {
    background-color: #08AEEA;
    background-image: linear-gradient(0deg, #08AEEA 0%, #2AF598 100%);
}

.bg5 {
    background-color: #FA8BFF;
    background-image: linear-gradient(45deg, #FA8BFF 0%, #2BD2FF 52%, #2BFF88 90%);

}

.bg6 {
    background-color: #4158D0;
    background-image: linear-gradient(43deg, #4158D0 0%, #C850C0 46%, #FFCC70 100%);
}

.bg7 {
    background-color: #3EECAC;
    background-image: linear-gradient(19deg, #3EECAC 0%, #EE74E1 100%);
}

.bg8 {
    background: #FF416C; /* fallback for old browsers */
    background: -webkit-linear-gradient(to right, #FF4B2B, #FF416C); /* Chrome 10-25, Safari 5.1-6 */
    background: linear-gradient(to right, #FF4B2B, #FF416C); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}

::-webkit-scrollbar {
    display: none
}

.App-header {
    width: 100vw;
    height: 8vh;
    display: flex;
    background-color: rgba(0, 0, 0, 0.2);
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-family: PingFangSC-Medium;
    font-size: calc(10px + 2vmin);
    color: white;
    transform: scale(1);
    box-shadow: 0 2px 10px 4px rgba(0, 0, 0, 0.3);
    transition: box-shadow 0.5s, transform 0.5s;
    position: fixed;
    left: 0;
    top: 0;
    user-select: none;
    text-shadow: 3px 2px 2px #333;
}

.header-txt {
    width: 100%;
    text-align: center;
    overflow: hidden;
    animation: typing 3s steps(60, end),
    blink-caret .3s step-end infinite alternate;
    white-space: nowrap;
}


@keyframes typing {
    from {
        width: 0;
    }
}

@keyframes blink-caret {
    50% {
        border-color: transparent;
    }
}

.App-header:hover {
    transform: scale(1.3);
    box-shadow: 0 0 40px 8px rgba(0, 0, 0, 0.3);
    transition: box-shadow 0.5s, transform 0.5s;
}

.App-footer {
    width: 100vw;
    height: 6vh;
    display: flex;
    background-color: rgba(0, 0, 0, 0.2);
    align-items: center;
    justify-content: center;
    font-family: PingFangSC-Medium;
    font-size: 12px;
    color: white;
    position: fixed;
    left: 0;
    bottom: 0;
}

.footer-icon {
    border-radius: 50%;
    border: 1px solid white;
}

.footer-tips {
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 12px;
    margin-left: 8px;
}

.poetry-box {
    width: 100vw;
    height: 86vh;
    margin-top: 8vh;
    padding: 60px 0;
    overflow-x: hidden;
    overflow-y: scroll;
    -webkit-overflow-scrolling: touch;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    flex-flow: column;
    color: white;
    font-size: calc(8px + 2vmin);
    box-sizing: border-box;
    text-shadow: 3px 3px 9px #333;
}

.poetry-chapter {
    font-size: calc(12px + 2vmin);
}

.poetry-title {
    font-size: calc(9px + 2vmin);
    margin-top: 18px;
}

.poetry-content {
    width: 100%;
    display: flex;
    flex-flow: column;
    justify-content: center;
    align-items: center;
    box-sizing: border-box;
    padding: 0 15px;
}

.poetry-content-item {
    width: 100%;
    box-sizing: border-box;
    margin-top: 10px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: calc(8px + 2vmin);
}